ks-container {
    display: block;
    padding: 0px;
    box-sizing: border-box;

    max-width: 1200px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
}

ks-container > * {
    margin: 20px 0px;
}

ks-container[center] {
    display: flex;
    justify-content: center;
    align-items: center;
}

@media only screen and (min-width: 1200px) {
    ks-container ~ ks-container > *:first-child {
        margin-top: 0px;
    }
}

@media only screen and (max-width: 1200px) {
    ks-container > * {
        margin: 15px 0px;
    }

    ks-container > *:first-child,
    ks-container > *:last-child {
        margin: 0px 0px;
    }

    ks-container[padding] > * {
        margin: 15px;
    }
}